

import Vue from 'vue'
import App from './App'
import router from './router'
import resource from 'vue-resource';
import Wang from './assets/js/function';
import WScroll from './components/Template/pullup';
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import dateFormat from 'vux/src/tools/date/format.js'


Vue.config.productionTip = false;

/*添加vux 全局组件 */
Vue.use(AlertPlugin);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin );
/*添加自定义配置与自定义方法 */
Vue.use(Wang);

/*配置 Vue Http请求插件 */
Vue.use(resource);


Vue.http.interceptors.push((request,next) => {
request.emulateJSON = true;
next(res => {

Vue.$vux.loading.hide();

if(!res.ok){

}
});
});


Vue.filter('filterTime',val => {
return Vue.prototype.$FilterTime(val,dateFormat);
});


Vue.component('WScroll',WScroll);

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
render : h => h(App)
});


