export default {
  name: 'app',
  data(){
    return {
      transname : 'slide-in',
    };
  },
  watch : {
    "$route"(){
      this.transname = this.$router.forwardStatus?'slide-in':'slide-out';
      this.$router.forwardStatus = false;
    }
  }
}



