export default {
    name: 'Nav_Template',
    props: ['active'],
    methods:{
        openView(active){
            if(active){
                this.$router.push('/Apply');
            }else if(this.$router.history.current.fullPath != '/'){
                this.$router.back();
            };
        }
    }
}



