export default {

  name : 'CompositeDetail',
  components: {},
  data(){
    return {info:{images:{}}};
  },
  mounted () {
    let id = this.$route.query._id;
    this.$http.get(this.$C.COMPOSITE_DETAIL + id + '/')
    .then(res => {
      return res.json();
    })
    .then(data => {
      if(data.code){
        this.$vux.toast.show({text : '服务器错误',type : 'warn'});
        return;
      };

      this.info = data.result;
    });
  }

};



