export default {
  name : 'Release',
  data(){
    return {
      val : ''
    };
  },
  methods:{
    releaseBtn(){
      if(!this.val){
        this.$vux.toast.show({text : '请填写食谱',type : 'warn'});
        return;
      };
      let _id = this.$route.query._id;
      this.$vux.loading.show();
      let url = this.$C.RECIPES + _id +'/';

      let subJson = {content : this.val};

      this.$http.post(url,subJson).then(res => {

        if(res.code){
          this.$vux.toast.show({text : '服务器出现错误',type : 'warn'});
          return;
        };

        this.$vux.toast.show({text : '操作成功',type : 'success'});
        setTimeout(() => {
          this.$router.go(-1);
        },1500);

      });

    }
  }
}



