
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'Diet',
components: {LoadMore},
data(){
return {
list : [],
next_start : 0,
loadingShow : true
};
},
props: ['_id'],
mounted () {

this.next_start = 0;
this.apiUrl = this.$C.DAILY + this._id;

this.$getData({api : this.apiUrl},res => {

this.$reGroup(res.dietetic_daily);

this.list = [...this.list,...res.dietetic_daily];
if(!res.dietetic_daily.length)this.$vux.toast.text('没有更多数据了');
});
},

methods : {
loadMore(loadEnd,disabled){
this.$getData({api : this.apiUrl,next_start : ++this.next_start},res => {
this.list = [...this.list,...res.dietetic_daily];
res.dietetic_daily.length?loadEnd():disabled();
});
}
}

}



