/*上拉加载组件 给予better-scroll封装*/
import BScroll from 'better-scroll';
export default {
    name : 'WScroll',
    data(){
        return {
            pullUpText : '上拉加载更多',
            textShow : false
        };
    },

    props : ['pullUpConfig','loadMore','reLoad','height'],

    mounted(){
        //拖动超过30触发上拉事件
        this.pullUpConfig.threshold = this.pullUpConfig.threshold || -30;
        //确保DOM渲染完成
        this.$nextTick(() => {

            //配置
            let ops = {
                //拖动时触发事件
                probeType : 2,
                click : true
            };

            //初始化内容区域高度 保证高度不够时也能拖动
            this.initContentHeight();
            //初始化插件
            this.scroll = new BScroll(this.$refs.container,ops);

            this.initEvent();
        });
    },

    methods : {
        //初始化内容区域高度
        initContentHeight(){
            //获取第一个元素 插件必须要有个根元素
            let container = this.$refs.container
            let contentDom = container.querySelector(':first-child')
            let cHeight = contentDom.offsetHeight;
            container.style.position = 'relative';
            container.style.overflow = 'hidden';
            container.style.height = this.height + 'px';
            contentDom.style.minHeight = this.height + 1 + 'px';
            // contentDom.style.paddingBottom = '60px';
            //contentDom.style.paddingBottom = '50px';
        },
        //初始化监听事件
        initEvent(){
            this.scroll.on('scroll',pos => {
                this.scrollEvent(pos);
            });

            this.scroll.on('touchEnd',pos => {
                this.pullUpText = '正在加载';
                this.touchEnd(pos);
            });
        },
        //监听滑动中
        scrollEvent(pos){
            if(pos.y <= this.scroll.maxScrollY){
                this.pullUpText = '手指松开加载更多';
                this.textShow = true;
            };
        },
        //监听手指松开
        touchEnd(pos){
            //取消监听
            //this.movingDirectionY -1 下拉 0 点击 1上拉
            if(this.scroll.movingDirectionY == 1 && pos.y <= -30){
                this.scroll.off('scroll');
                this.scroll.off('touchEnd');
                this.scroll.refresh();
                this.loadMore&&this.loadMore(this.loadEnd,this.disabled);
            }else if(this.scroll.movingDirectionY == -1 && pos.y > 30){
                this.reLoad&&this.reLoad(this.loadEnd,this.disabled);
            };
        },
        //数据加载完成 用户调用重置上拉
        loadEnd(){
            this.pullUpText = '上拉加载更多';
            // this.scroll.refresh();
            this.initEvent();
        },

        //数据全部加载完成 禁用上拉更多
        disabled(){
            this.scroll.off('scroll');
            this.scroll.off('touchEnd');
            this.pullUpText = '没有更多了';
            this.textShow = true;
            console.log('禁用了');
        }
        
    }
};




