
import LoadMore from 'vux/src/components/load-more/index.vue'

import NavFooter from '@/components/Template/Nav';
export default {
name:'MyCoustomer',
components: {NavFooter,LoadMore},
data(){
return {
users : [],
wrapperHeight : 0,
next_start : 0
};
},

methods : {
loadMore(loadEnd,disabled){
++this.next_start
this.$getData({
api : this.$C.MY_CLIENTS,
method : 'POST',
next_start : this.next_start
},res => {
this.users = [...this.users,...res.users];

res.users.length?loadEnd():disabled();
});
}
},
mounted () {
this.wrapperHeight = document.documentElement.clientHeight - document.querySelector('nav').offsetHeight - 30;

this.$getData({
api : this.$C.MY_CLIENTS,
method : 'POST'
},res => {
this.users = [...this.users,...res.users];
});
}
}



