
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name : 'DietDetail',
components: {
LoadMore
},
data(){
return {
list : [],
loadingShow : true,
};
},
mounted () {
let id = this.$route.query._id;
this.$http.get(this.$C.DDAILU_DETAIL + id + '/')
.then(res => {
this.loadingShow = false;
return res.json();
})
.then(data => {
if(data.code){
this.$vux.toast.show({text : '服务器错误',type : 'warn'});

};

if(!data.result.dietetics.length){
this.$vux.toast.text('没有更多数据了');
};

this.list = [...this.list,...data.result.dietetics];
});

}
};




