export default {
    name : 'ApplyDetail',
    data(){
        return {
            info : {},
            disabled : false,
            btnText : '接受申请',
            id : ''
        };
    },
    mounted () {
        this.id = this.$route.query._id;
        this.$vux.toast.show({text:'加载中',type : 'text',isShowMask:true,time:99999});
        this.$http.get(this.$C.APPLY_DETAIL + this.id + '/')
        .then(res => {
            this.$vux.toast.hide();
            return res.json();
        })
        .then(data => {
            if(data.code){
                this.$vux.toast.show({text : '服务器错误',type : 'warn'});
                return;
            };

            this.info = data.result;
        });
    },

    methods : {

        accept (){
            let subJson = {is_agree : true};
            this.$vux.loading.show({text: '请稍后'});
            this.$http.post(this.$C.CREATE_APPPLY + this.id +'/',subJson)
            .then(res => {
                this.$vux.loading.hide();
                return res.json();
            })
            .then(data => {
                if(data.code){
                    this.$vux.toast.show({text : '操作失败',type : 'warn'});
                }else{
                    this.$vux.toast.show({type : 'success'});
                    this.disabled = true;
                    this.btnText = '操作成功';
                };
            });
        }

    }
};



