import querystring from 'vux/src/tools/querystring/index.js'

import { type } from 'os';

const HOST = "";

let APIS = {
MY_CLIENTS : HOST + '/seller/my_clients/',
RECIPES : HOST + '/seller/recipes/',
USER_RECIPES : HOST + '/seller/user_recipes/',
DAILY : HOST + '/seller/sell_dietetic_daily/',
DDAILU_DETAIL : HOST + '/user/get/dietetic_daily_info/',
COMPOSITELIST : HOST + '/seller/sell_comprehensive_daily/',
COMPOSITE_DETAIL : HOST + '/user/get/user_comprehensive_info/',
APPLY_CLIENTS : HOST + '/seller/apply_clients/',
APPLY_DETAIL : HOST + '/seller/apply_clients_info/',
CREATE_APPPLY : HOST + '/seller/create_apply/'
};

let PULLUPCONFIG = {


content : '',
upContent : '',
downContent : '松开手指加载更多',
loadingContent : '加载中'
};


let filterTime = (timed,dateFormat) => {


let newDate = new Date();
let s1 , s2 ,s3 = null;
let val = '';
s1 = getTimed();
s2 = getTimed(-1);
s3 = getTimed(-2);


if(timed > s1[0] && timed < s1[1]){

val = '今天';
}else if(timed > s2[0] && timed < s2[1]){

val = '昨天';
}else if(timed > s3[0] && timed < s3[1]){

val = '前天';
}else {
val = dateFormat?dateFormat(timed * 1000,'YYYY-MM-DD'):timed;
};

return val;
};


let getTimed = today => {
let newDate = new Date();
let t = [];
today = today || 0;
newDate.setDate(newDate.getDate() + today);

newDate.setHours(0);
newDate.setMinutes(0);
newDate.setSeconds(0);

t[0] = parseInt(newDate.getTime() / 1000);


newDate.setHours(23);
newDate.setMinutes(59);
newDate.setSeconds(59);

t[1] = parseInt(newDate.getTime() / 1000);

return t;
};


let getData = function({
next_start = 0,
method = 'GET',
subJson = {},
api = ''
},cb) {
this.$http({
url : api + '/?next_start=' + next_start,
method : method,
body : subJson
})
.then(res => {
return res.json();
})
.then(data => {

if(data.code){
this.$vux.toast.show({text : '服务器错误',type : 'warn'});
return;
};

cb&&cb(data.result);
});
};


let routerLink = function(path,query) {
let link = path + '?' + querystring.stringify(query);
this.$router.push(link);
};


let reGroup = function(arr){

let newArr = [];
let s1,s2,s3;
let nowDate = new Date();
nowDate.setDate(nowDate.getDate() + 0);
s1 = parseInt(nowDate.getTime() / 1000);
nowDate.setDate(nowDate.getDate() + -1);
s2 = parseInt(nowDate.getTime() / 1000);
nowDate.setDate(nowDate.getDate() + -1);
s3 = parseInt(nowDate.getTime() / 1000);

for(let item of arr){

switch(filterTime(item.timed)){
case '今天' :
s1 = null;
break;
case '昨天' :
s2 = null;
break;
case '前天' :
s3 = null;
break;
};

};

if(s3) arr.unshift({timed : s3,is : true});
if(s2) arr.unshift({timed : s2,is : true});
if(s1) arr.unshift({timed : s1,is : true});

arr.sort(function (a,b){
return a.timed > b.timed?-1:1;
});

return arr;
};

export default {

install (Vue,options){

/*接口地址配置 */
Vue.prototype.$C = APIS;
/*上拉加载配置 */
Vue.prototype.$PULLUPCONFIG = PULLUPCONFIG;
/*时间戳辅助 */
Vue.prototype.$FilterTime = filterTime;

Vue.prototype.$getData = getData;

Vue.prototype.$routerLink = routerLink;

Vue.prototype.$reGroup = reGroup;
}
}


