
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
name : 'History',
components : {LoadMore},
data(){
return {
showIndex : null,
list : [],
next_start : 0,
};
},
props: ['_id'],
methods : {
showPanel(index){
let lis = document.querySelectorAll('.w-list li');
lis.forEach(item => {
item.classList.remove('showPanel');
});

if(this.showIndex == index){
this.showIndex = null;
lis[index].classList.remove('showPanel');
}else{
this.showIndex = index;
lis[index].classList.add('showPanel');
};
},

loadMore(loadEnd,disabled){

this.$getData({api : this.apiUrl,next_start : ++this.next_start},res => {
this.list = [...this.list,...res.today_recipes];

res.today_recipes.length?loadEnd():disabled();
});

},
Reload(loadEnd,disabled){

}
},
mounted () {
this.next_start = 0;
this.apiUrl = this.$C.USER_RECIPES + this._id;

this.$getData({api : this.apiUrl},res => {

this.$reGroup(res.today_recipes);

this.list = [...this.list,...res.today_recipes];

if(!res.today_recipes.length)this.$vux.toast.text('没有更多数据了');
});
}
}



