
import LoadMore from 'vux/src/components/load-more/index.vue'

import NavFooter from '@/components/Template/Nav';
export default {
name : 'Apply',
components: {NavFooter,LoadMore},
data(){
return {
list : [],
next_start:0,
loadingShow : true
};
},
mounted () {

this.getData();
},
methods : {

getData(next_start,cb){

next_start = next_start || 0;

this.$http.get(this.$C.APPLY_CLIENTS + '/?next_start=' + next_start)
.then(res => {
this.loadingShow = false;
return res.json();
})
.then(data => {
if(data.code){
this.$vux.toast.show({text : '服务器错误',type : 'warn'});
return;
};

this.list = [...this.list,...data.result.apply_clients];

if(!data.result.apply_clients.length){
this.$vux.toast.show({text : '没有数据了'});
}else{
cb&&cb();
};

});
}
}
}



