
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name : 'Composiite',
components: {LoadMore},
data (){
return {
list : [],
info : {},
next_start : 0,
loadingShow : true
};
},
props: ['_id'],
mounted () {
this.next_start = 0;
this.apiUrl = this.$C.COMPOSITELIST + this._id;

this.$getData({api : this.apiUrl},res => {

this.$reGroup(res.comprehensive);

this.list = [...this.list,...res.comprehensive];
this.info = {
local_weight :res.local_weight,
arrange_weight : res.local_weight,
today_weight : res.today_weight
};

if(!res.comprehensive.length)this.$vux.toast.text('没有更多数据了');

});
},
methods: {

loadMore(loadEnd,disabled){

this.$getData({api : this.apiUrl,next_start : ++this.next_start},res => {
this.list = [...this.list,...res.comprehensive];
res.comprehensive.length?loadEnd():disabled();
});
}
}
};



